/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/

/* Links */

a {
	color: $color__link;
	text-decoration: none;

	&:visited {
		color: $color__link-visited;
	}
	&:hover,
	&:focus,
	&:active {
		color: $color__link-hover;
	}
	&:focus {
		outline: thin dotted;
	}
	&:hover,
	&:active {
		outline: 0;
	}
}

/* Menus */
.site-footer {
	width: 100%; 
	margin: 4.5rem 0 3rem;

	.menu {
		width: inherit; 
		list-style-type: none;

		ul {
			padding: 0; 
		}

		li {
			display: inline;

			&:not(:last-child):after {
				content: '|';
				padding: 0 1rem;
			}

			.link:last-child:after {
				content: '';
			}
		}
	}
}



